import React, { useEffect, useState, useRef} from 'react';
import html2canvas from "html2canvas";
import data from './data/data'
import './App.css';

function App() {
  const link = useRef(null)
  const [info, setInfo] = useState(data)
  const {common, company, style, preview, font} = info
  const [file, setFile] = useState()
  const [isOpen, setOpen] = useState(false)

  const onChange = (one, value, params) => {
    const item = info[one].find(el => el.params === params)
    item.value = value
    setInfo({...info})
  }

  const onChangeCheckbox = (params, index) => {
    const item = info[params]
    item.forEach(el => el.value = false)
    item[index].value = true
    setInfo({...info, [params]: item})
  }

  // useEffect(() => {
  //   const comp = company.reduce(function(previousValue, currentValue) {
  //     const prevValue = previousValue.text ? previousValue.text + previousValue.value : previousValue
  //     const currValue = currentValue.text + currentValue.value
  //     return `${prevValue}\n${currValue}`;
  //   });
  //   const comm = common.reduce(function(previousValue, currentValue) {
  //     const prevValue = previousValue.text ? previousValue.text + previousValue.value : previousValue
  //     const currValue = currentValue.text + currentValue.value
  //     return `${prevValue}\n${currValue}`;
  //   });
  //   let st = ''
  //   style.forEach(el => el.value && (st = `Стиль логотипа: ${el.text}\n`))
  //   let prev = ''
  //   preview.forEach(el => el.value && (prev = `Вид логотипа: ${el.text}\n`))
  //   let fon = ''
  //   font.forEach(el => el.value && (fon = `Стиль шрифта: ${el.text}`))
  //   const f = new Blob([comp + comm + st + prev + fon], {type : 'txt'})
  //   setFile(f)
  // }, [common, company, font, info, preview, style])

  const onClick = () => {
    html2canvas(document.querySelector('body')).then(canvas => {
      canvas.toBlob(function(blob) {
        setFile(URL.createObjectURL(blob))
        link.current.click()
        setOpen(true)
      })
    });
  }

  return (
    <div className="App" id='page'>
      <h1>Бриф на разработку логотипа</h1>
      <div>
        <h2>Информация для обратной связи</h2>
        {company.map(el => <div key={el.params} className="inputBox">
          <p>{el.text}</p>
          <input onChange={(e) => onChange('company', e.target.value, el.params)} type={el.type || "text"} value={el.value}/>
        </div>)}
      </div>
      <div>
        <h2>Информация о логотипе</h2>
        {common.map(el => <div key={el.params}  className="inputBox">
          <p>{el.text}</p>
          <span>{el.description}</span>
          {el.input ?
            <input onChange={(e) => onChange('common', e.target.value, el.params)} type="text" value={el.value}/>
            : <textarea onChange={(e) => onChange('common', e.target.value, el.params)} type="text" value={el.value}/>}
        </div>)}
      </div>
      <div>
        <h2>Общий стиль</h2>
        {style.map((el, index) => <div  key={el.params} className="inputBox flex">
          <div onClick={() => onChangeCheckbox('style', index)} className={`checkbox ${el.value ? 'active' : ''}`}/>
          <p>{el.text}</p>
        </div>)}
      </div>
      <div>
        <h2>КАК, ПО-ВАШЕМУ, ДОЛЖЕН ВЫГЛЯДЕТЬ ВАШ ЛОГОТИП?</h2>
        {preview.map((el, index) => <div key={el.params}  className="inputBox flex space-between">
          <div>
            <div onClick={() => onChangeCheckbox('preview', index)} className={`checkbox ${el.value ? 'active' : ''}`}/>
            <p>{el.text}</p>
          </div>
          <img src={el.img} alt=""/>
        </div>)}
      </div>
      <div>
        <h2>ПОЖЕЛАНИЯ ПО ШРИФТОВОЙ ЧАСТИ ЛОГОТИПА</h2>
        {font.map((el, index) => <div key={index} className="inputBox flex space-between">
          <div>
            <div onClick={() => onChangeCheckbox('font', index)} className={`checkbox ${el.value ? 'active' : ''}`}/>
            <p>{el.text}</p>
          </div>
          <img src={el.img} alt=""/>
        </div>)}
      </div>
      <div className='save' onClick={onClick}>Сохранить</div>
      <a ref={link} href={file} download='briff.png' target="_blank"></a>
      <div className={`modalBox ${isOpen ? 'open' : ''}`}>
        <div className="modal">
          <p>Отправьте нам сохраненное изображение.
            <span onClick={() => setOpen(false)}>&#215;</span>
          </p>

        </div>
      </div>
      <div className={`modalCircle ${isOpen ? 'open' : ''}`}></div>
    </div>
  );
}

export default App;
